#toursbase {
    background: linear-gradient(rgba(black, 0.15), rgba(black, 0.15)), url(../img/toursbase-intro.png);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
}
#excursions {
    background: linear-gradient(rgba(black, 0.15), rgba(black, 0.15)), url(../img/excursions-intro.png);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
}

.page-intro {
    height: calc(100vh - 128px);
    width: 100%;
    .container {
        height: 100%;
        width: 100%;
        position: relative;
    }
    &__inner {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__link {
        &:hover {
            text-decoration: underline;
        }
    }

    &__title {
        text-align: center;
        color: white;
        font-size: calc(29px + 15 * (100vw / 1370));
        line-height: calc(39px + 19 * (100vw / 1370));
        @media (max-width: 736px) {
            font-size: calc(29px + 15 * ((100vw - 320) / 1370));
        }
    }
}
